.App {
  text-align: center;
}

.navbar {
  background-color: #fff; 
  border-bottom: 2px solid #ccc; 
  font-family: 'Arial', sans-serif; 
}

.navbar-brand {
  font-size: 1.5rem; 
  font-weight: bold;
  color: #333; 
}

.nav-link {

  font-size: 1rem;
  font-weight: 500;
  color: #555;
  margin-right: 15px;
  margin-bottom: 10px;
}

.nav-link:hover {
  color: #000; 
}


.empresa-info {
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.empresa-info h2 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.info {
  margin-bottom: 15px;
}

.info h3 {
  font-size: 18px;
  margin-bottom: 8px;
  color: #555;
}

.info p {
  font-size: 16px;
  color: #777;
}
img{
  width: 300px;
}
.carousel-container{
  margin: 50px;
  width: 500px;
  height:300px
  
  
}
a{
   margin-top: 20px;
}


footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #333;
  text-align: center;
  padding: 10px 0;
}






